import { createClient } from '@supabase/supabase-js';
import { createBrowserClient } from '@supabase/ssr';

const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL!;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!;

// Client-side Supabase client
export const supabase = createClient(supabaseUrl, supabaseAnonKey);

// Browser client for SSR
export const createSupabaseBrowserClient = () => {
  return createBrowserClient(supabaseUrl, supabaseAnonKey);
};

// Database Tables Schema Types
export type Database = {
  public: {
    Tables: {
      users: {
        Row: {
          id: string;
          email: string;
          first_name: string;
          last_name: string;
          role: 'reader' | 'author' | 'admin';
          avatar_url?: string;
          bio?: string;
          created_at: string;
          updated_at: string;
          is_active: boolean;
          // Author specific
          author_verified?: boolean;
          total_books?: number;
          total_sales?: number;
          total_revenue?: number;
          // Reader specific
          followed_authors?: string[];
          purchased_books?: string[];
        };
        Insert: {
          id?: string;
          email: string;
          first_name: string;
          last_name: string;
          role: 'reader' | 'author' | 'admin';
          avatar_url?: string;
          bio?: string;
          created_at?: string;
          updated_at?: string;
          is_active?: boolean;
          author_verified?: boolean;
          total_books?: number;
          total_sales?: number;
          total_revenue?: number;
          followed_authors?: string[];
          purchased_books?: string[];
        };
        Update: {
          id?: string;
          email?: string;
          first_name?: string;
          last_name?: string;
          role?: 'reader' | 'author' | 'admin';
          avatar_url?: string;
          bio?: string;
          updated_at?: string;
          is_active?: boolean;
          author_verified?: boolean;
          total_books?: number;
          total_sales?: number;
          total_revenue?: number;
          followed_authors?: string[];
          purchased_books?: string[];
        };
      };
      books: {
        Row: {
          id: string;
          title: string;
          author_name: string;
          author_id: string;
          price: number;
          image_url?: string;
          description: string;
          category: string;
          cover_type: string;
          stock: number;
          reorder_point: number;
          book_file_url?: string;
          published_at: string;
          updated_at: string;
          sales: number;
          revenue: number;
          average_rating: number;
          total_reviews: number;
        };
        Insert: {
          id?: string;
          title: string;
          author_name: string;
          author_id: string;
          price: number;
          image_url?: string;
          description: string;
          category: string;
          cover_type: string;
          stock: number;
          reorder_point: number;
          book_file_url?: string;
          published_at?: string;
          updated_at?: string;
          sales?: number;
          revenue?: number;
          average_rating?: number;
          total_reviews?: number;
        };
        Update: {
          title?: string;
          author_name?: string;
          price?: number;
          image_url?: string;
          description?: string;
          category?: string;
          cover_type?: string;
          stock?: number;
          reorder_point?: number;
          book_file_url?: string;
          updated_at?: string;
          sales?: number;
          revenue?: number;
          average_rating?: number;
          total_reviews?: number;
        };
      };
      orders: {
        Row: {
          id: string;
          user_id: string;
          books: any;
          total_amount: number;
          status: string;
          payment_method: string;
          payment_gateway: string;
          payment_id?: string;
          order_id?: string;
          created_at: string;
          completed_at?: string;
          shipping_address?: any;
        };
        Insert: {
          id?: string;
          user_id: string;
          books: any;
          total_amount: number;
          status: string;
          payment_method: string;
          payment_gateway: string;
          payment_id?: string;
          order_id?: string;
          created_at?: string;
          completed_at?: string;
          shipping_address?: any;
        };
        Update: {
          status?: string;
          payment_id?: string;
          completed_at?: string;
        };
      };
      reviews: {
        Row: {
          id: string;
          book_id: string;
          user_id: string;
          username: string;
          rating: number;
          comment: string;
          created_at: string;
          author_reply?: string;
          reader_follow_up?: string;
        };
        Insert: {
          id?: string;
          book_id: string;
          user_id: string;
          username: string;
          rating: number;
          comment: string;
          created_at?: string;
          author_reply?: string;
          reader_follow_up?: string;
        };
        Update: {
          rating?: number;
          comment?: string;
          author_reply?: string;
          reader_follow_up?: string;
        };
      };
      follows: {
        Row: {
          id: string;
          reader_id: string;
          author_id: string;
          followed_at: string;
        };
        Insert: {
          id?: string;
          reader_id: string;
          author_id: string;
          followed_at?: string;
        };
        Update: {};
      };
    };
  };
};

export default supabase;